function getState(section) {
  const data = window.localStorage.getItem("bsa_data");
  if (data && JSON.parse(data)["likes"]) {
    return JSON.parse(data)["likes"][section];
  } else {
    return "false";
  }
}
function setState(section, state) {
  const data = window.localStorage.getItem(`bsa_data`);
  if (data) {
    const store = JSON.parse(data);
    store["likes"] = store["likes"] || {};
    store["likes"][section] = state;
    window.localStorage.setItem("bsa_data", JSON.stringify(store));
  } else {
    const store = {};
    store["likes"] = store["likes"] || {};
    store["likes"][section] = state;
    window.localStorage.setItem("bsa_data", JSON.stringify(store));
  }
}

function like() {
  const button = this;
  if (button && button.dataset.liked !== "true") {
    // send mixpanel event here
    button.dataset.liked = "true";
    setState(button.dataset.section, "true");
  }
}

window.addEventListener("load", () => {
  const buttons = document.querySelectorAll(".i-like-it-btn");
  buttons.forEach((button) => {
    const section = button.dataset.section;
    button.dataset.liked = getState(section);
    button.addEventListener("click", like.bind(button));
  });
});
